.blog{
    .section__title{
        margin-bottom: 80px;
    }
    &__post{
        border-radius: 6px;
        overflow: hidden;
        background: $white;
        margin-bottom: 50px;
        box-shadow: $main-shadow;
        .post-content{
            padding: 30px;
            .post-title{
                margin-bottom: 20px;
            }
            .post-excerpt{
                margin-bottom: 20px;
            }
            a{
                font-family: $primary-font;
                color: $text-color-dark;
                font-size: 18px;
                font-weight: 500;
            }
        }
        &.featured{
            display: flex;
            align-items: center;
            padding: 40px;
            .post-thumb{
                flex-basis: 40%;
            }
            .post-content{
                flex-basis: 60%;
                padding: 0 0 0 40px;
            }
        }
    }
}